<template>
  <div class="envEcharts">
    <div ref='envEcharts'></div>
  </div>
</template>

<script>
export default {
  name: 'componentspage2',
  data() {
    return {
      input: '',
      book: '111111',
      showData: [
        {
          title: '广州银行',
          item: [
            {
              id: 1,
              position: 1,
              name: ' 主机',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 1,
              name: '服务器1',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 2,
              name: ' 服务器2',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 3,
              name: ' 服务器3',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 1,
              name: ' 硬盘A',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 2,
              name: ' 硬盘B',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 3,
              name: ' 硬盘C',
              content: '102.102.124'
            }
          ],
          link: [
            {
              source: 0,
              target: 1
            },
            {
              source: 0,
              target: 2
            },
            {
              source: 0,
              target: 3
            },
            {
              source: 1,
              target: 4
            },
            {
              source: 2,
              target: 5
            },
            {
              source: 3,
              target: 6
            }
          ]
        },
        {
          title: '西安银行',
          item: [
            {
              id: 1,
              position: 1,
              name: ' 主机',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 1,
              name: '服务器1',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 2,
              name: ' 服务器2',
              content: '102.102.124'
            },
            {
              id: 2,
              position: 3,
              name: ' 服务器3',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 1,
              name: ' 硬盘A',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 2,
              name: ' 硬盘B',
              content: '102.102.124'
            },
            {
              id: 3,
              position: 3,
              name: ' 硬盘C',
              content: '102.102.124'
            }
          ],
          link: [
            {
              source: 0,
              target: 1
            },
            {
              source: 0,
              target: 2
            },
            {
              source: 0,
              target: 3
            },
            {
              source: 1,
              target: 4
            },
            {
              source: 2,
              target: 5
            },
            {
              source: 3,
              target: 6
            }
          ]
        }
      ],
      width: '100%',
      ids: ''
    }
  },
  created() {},
  mounted() {
    this.handledata()
    window.addEventListener('resize', () => {
      this.handleSep()
    })
  },
  methods: {
    getdata(data, link, el, title) {
      // let _this = this
      let echarts = this.$echarts
      let option = {
        title: {
          text: title + '组视图',
          top: 'top',
          left: 'center'
        },
        tooltip: {
          formatter: params => {
            this.input = params
            let content = `
              <div><span>${params.name}:</span><span>${
              params.data.content
            }</span></div>
              <div><span>${params.name}:</span><span>${
              params.data.content
            }</span></div>
            `
            return content
          }
        },
        itemStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
              {
                offset: 0,
                color: '#157eff'
              },
              {
                offset: 1,
                color: '#35c2ff'
              }
            ])
          },
          shadowBlur: 10
        },
        textStyle: {
          color: '#333',
          fontSize: 16,
          fontWeight: 600
        },
        legend: [
          {
            formatter: function(name) {
              return echarts.format.truncateText(name, 200, '18px', '…')
            },
            tooltip: {
              show: true
            },
            selectedMode: 'false',
            bottom: 20
          }
        ],
        animationDuration: 1500,
        animationEasingUpdate: 'quinticInOut',
        xAxis: {
          show: false,
          type: 'value'
        },
        yAxis: {
          show: false,
          type: 'value'
        },
        series: [
          {
            type: 'graph',
            coordinateSystem: 'cartesian2d',
            legendHoverLink: false, // 是否启用图例 hover 时的联动高亮。
            hoverAnimation: true, // 是否开启鼠标 hover 节点的提示动画效果
            nodeScaleRatio: false,
            edgeSymbol: ['none', 'none'], // 边两端的标记类型
            edgeSymbolSize: [2, 15], // 边两端的标记类型大小
            edgeLabel: {
              // 是否显示标签。
              show: false,
              normal: {
                show: false,
                position: 'middle', // 标签位置
                textStyle: {
                  fontSize: 12
                },
                formatter: '{b}'
              }
            },
            focusNodeAdjacency: true, // 是否在鼠标移到节点上的时候突出显示节点以及节点的边和邻接节点
            label: {
              // 圆形上面的文字
              normal: {
                position: 'inside',
                show: false,
                textStyle: {
                  fontSize: 12
                }
              }
            },
            itemStyle: {
              // 数据点的颜色
              show: false,
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                  {
                    offset: 0,
                    color: '#157eff'
                  },
                  {
                    offset: 1,
                    color: '#35c2ff'
                  }
                ])
              },
              shadowBlur: 10
            },
            lineStyle: {
              // 线性的颜色
              normal: {
                width: 1,
                shadowColor: 'none',
                color: '#ff0000'
              }
            },
            data: data,
            links: link
          }
        ]
      }
      this.$echarts.init(el).setOption(option)
      this.$echarts.init(el).resize()
      // console.log(this.$echarts.init(el).getOption())
    },
    handleSep() {
      this.showData.forEach((value, index) => {
        let id = document.getElementById('echarts' + index)
        this.getdata(value.item, value.link, id, value.title)
      })
    },
    handledata() {
      // let _this = this
      this.showData.forEach((value, key) => {
        value.item.forEach((single, index) => {
          let book = {
            name: single.name,
            value: [(single.id - 1) * 100, single.position * 100],
            // symbol: 'rect', // 形状
            symbol: 'image:///static/no1.png',
            symbolSize: [40, 30], // 宽和高
            content: single.content
          }
          value.item[index] = book
        })
        let el = `<div class='el-row'><div id='echarts${key}' style="height: 400px;width: ${
          this.width
        }"></div></div>`
        this.ids += el
        this.$refs.envEcharts.innerHTML = this.ids
      })
      this.handleSep()
    }
  }
}
</script>

<style lang="stylus" scoped>
.echarts
  height: 400px;width: 100%;
</style>
